$side-bar-width-desktop: 260px;
$side-bar-width-wide: 300px;
$layout-gap-inline: var(--grail-layout-gap-inline);
$layout-gap-block: var(--grail-layout-gap-block);

@import "./debug.scss";
@import "./src/twin.scss";
@import "./src/standard.scss";
@import "./src/single.scss";

// Site resets
html {
  box-sizing: border-box !important;
  --grail-layout-gap-inline: 0rem;
  --grail-layout-gap-block: 0rem;
  --sidebars-width: 280px;
  --sidebars-width-wide: 350px;
  --desktop-query: var(--main-column-width) + var(--sidebars-width) * 2;
}
